* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background: url("bg.4183f79e.webp") center / cover no-repeat fixed;
  min-height: 100vh;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
}

body:before {
  content: "";
  z-index: 1;
  background-color: #000000bf;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

header, main {
  z-index: 2;
  position: relative;
}

header {
  text-align: center;
  color: #fff;
  background-color: #000000e6;
  border-bottom: 5px solid #07c;
  padding: 20px;
  box-shadow: 0 8px 30px #000000b3;
}

h1 {
  letter-spacing: 5px;
  margin-bottom: 10px;
  font-family: Frank Ruhl, serif;
  font-size: 3em;
  font-weight: 700;
}

.subtitle {
  color: #ffffffe6;
  margin-top: 10px;
  font-family: Roboto, sans-serif;
  font-size: 1.8em;
  font-weight: 400;
}

.content-section {
  text-align: center;
  max-width: 1200px;
  margin: 40px auto;
  padding: 20px;
}

h2 {
  color: #eee;
  text-shadow: 2px 2px 6px #000000b3;
  margin-bottom: 30px;
  font-size: 3em;
}

.grid-container {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 40px;
  padding: 0 20px;
  display: grid;
}

.story {
  background-color: #fffffff2;
  border-radius: 20px;
  transition: transform .3s, box-shadow .3s, background-color .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px #0006;
}

.story:hover {
  background-color: #fff;
  border: 2px solid #07c;
  transform: translateY(-10px);
  box-shadow: 0 15px 30px #0009;
}

img {
  object-fit: cover;
  filter: grayscale();
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  width: 100%;
  height: 250px;
  transition: all .35s;
  box-shadow: inset 0 0 15px #0006;
}

img:hover {
  filter: grayscale(0);
  transform: scale(1.05);
}

.story h3 {
  color: #333;
  text-shadow: 1px 1px 2px #ffffffe6;
  margin: 20px 15px;
  font-size: 2.2em;
}

.story p, .item p {
  color: #555;
  margin: 15px;
  font-size: 1.1em;
}

.item:hover {
  background-color: #fff;
  border: 2px solid #07c;
  transform: translateY(-10px);
  box-shadow: 0 15px 30px #0009;
}

.item {
  background-color: #fffffff2;
  border-radius: 20px;
  transition: transform .3s, box-shadow .3s, background-color .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 30px #0006;
}

.item h3 {
  color: #333;
  text-shadow: 1px 1px 2px #ffffffe6;
  margin: 20px 15px;
  font-size: 2.2em;
}

a {
  color: inherit;
  text-decoration: none;
  transition: color .3s;
}

a:hover h3 {
  color: #07c;
  text-decoration: underline;
}

footer {
  text-align: center;
  color: #fff;
  z-index: 2;
  background-color: #000c;
  padding: 20px;
  font-size: 1em;
  position: relative;
  box-shadow: 0 -2px 10px #0009;
}

@media (width <= 600px) {
  h2 {
    font-size: 2.5em;
  }

  .grid-container {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  }

  img {
    height: 200px;
  }
}
